:root {
  --primary-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
}

h1, h2, h3, h4 p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

.container {
  width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 479px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.header-text {
  font-family: var(--secondary-font);
  text-transform: uppercase;
  color: #9acd32;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.33;
}

